@font-face {
  font-family: 'PoppinsRegular';
  src: url('/fonts/Poppins/Poppinsregular.woff') format('woff'),
    url('/fonts/Poppins/Poppinsregular.woff2') format('woff'),
    url('/fonts/Poppins/Poppinsregular.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
}

